var config = {
    // outputlog on
    outputlog: false
};

function log(type, info) {
    if (config.outputlog) {
        console.log('[' + type + '] ' + info);
    }
}

function setUint16(buf, start, val) {
    buf[start] = (val & 0x00ff);
    buf[start + 1] = ((val & 0xff00) >> 8);
};

function getUint16(buf, start) {
    return buf[start] + (buf[start + 1] << 8);
};

function getUint32(buf, start) {
    return buf[start] + (buf[start + 1] << 8) + (buf[start + 2] << 16) + (buf[start + 3] << 24);
};

function setUint32(buf, start, val) {
    buf[start] = (val & 0x000000ff);
    buf[start + 1] = ((val & 0x0000ff00) >> 8);
    buf[start + 2] = ((val & 0x00ff0000) >> 16);
    buf[start + 3] = ((val & 0xff000000) >> 24);
};

function appendBytes(buf1, buf2) {
    let buf = new Uint8Array(buf1.length + buf2.length);
    buf.set(buf1, 0);
    buf.set(buf2, buf1.length);
    return buf;
};

export {
    config,
    log,
    setUint16,
    getUint16,
    setUint32,
    getUint32,
    appendBytes
}
