import { dtsdkserv as pb } from './proto';
import { log,
    setUint16,
    getUint16,
    setUint32,
    getUint32,
    appendBytes } from './util';

export var WebSocket = WebSocket || window.WebSocket || window.MozWebSocket;
export var HEADERLEN = 4;

// LittleEndian

export class DTCommonClient {
    /**
     * ---start
     * **/
    constructor(addr) {
        log('info', 'create DTCommonClient Instance.');

        this.servAddr = addr;
        this.ws = null;
        this.msgbuf = null;

        //
        this.version = "1.0.6";

        //
        this.isConnecting = false;

        //
        this.heartTimer = null;
        //
        this.heartTime = 30;
        //
        this.heartAcceptTime = 10;
        //
        this.hasAcceptMsg = false;
        //
        this.reconnectTimes = 0;
        //
        this.isByActive = false;

        this.onWebSocketOpenCallBack = null;
        this.onWebSocketOnMessage = null;
        this.onWebSocketOnError = null;
        this.onWebSocketOnClose = null;

        this.clientdata = [];
        this.initdata();
    }
    initdata(){
        this.clientdata = [];
        var MainTypeArr = Object.keys(pb.MainType);
        for(var i = 0; i < MainTypeArr.length; i++){
            this.clientdata.push([]);
        }
        var SubBaseTypeArr = Object.keys(pb.SubBaseType);
        for(var i = 0; i < SubBaseTypeArr.length; i++){
            var reqdecode = null;
            var rspdecode = null;
            if(i == 0){
                reqdecode = pb.KeepAliveRequest;
            }
            if(i == 1){
                rspdecode = pb.KeepAliveResponse;
            }
            if(i == 2){
                rspdecode = pb.ErrResponse;
            }
            if(i == 3){
                rspdecode = pb.CmdRet;
            }
            this.clientdata[0].push([null,reqdecode,rspdecode]);
        }
        var SubAccountTypeArr = Object.keys(pb.SubAccountType);
        for(var i = 0; i < SubAccountTypeArr.length; i++){
            var reqdecode = null;
            var rspdecode = null;
            if(i == 0){
                reqdecode = pb.LoginRequest;
            }
            if(i == 1){
                rspdecode = pb.LoginResponse;
            }
            this.clientdata[1].push([null,reqdecode,rspdecode]);
        }
        var SubGameTypeArr = Object.keys(pb.SubGameType);
        for(var i = 0; i < SubGameTypeArr.length; i++){
            var reqdecode = null;
            var rspdecode = null;
            if(i == 0){
                rspdecode = pb.FreeSpinRsp;
            }
            if(i == 1){
                rspdecode = pb.FreeSpin;
            }
            if(i == 2){
                reqdecode = pb.ChangeScreenRequest;
            }
            this.clientdata[2].push([null,reqdecode,rspdecode]);
        }
        log("length",this.clientdata.length);
    }
    pushCallBack(maintype,subtype,callback) {
        if(this.clientdata[maintype] && this.clientdata[maintype][subtype]){
            this.clientdata[maintype][subtype][0] = callback;
        }else {
            log("bucunzai","");
        }
    }
    getCallBack(header,buf){
        var msg = "";
        if(buf){
            msg = this.clientdata[header.maintype][header.subtype][2].decode(buf,header.len);
        }
        if(this.clientdata[header.maintype][header.subtype][0]) {
            this.clientdata[header.maintype][header.subtype][0](msg);
        }else {
            log("bucunzai1","");
        }
    }
    //
    connect() {
        if(this.isConnecting){
            return;
        }
        log('info', 'connect dtapiserv ' + this.servAddr + ' ...');

        this.ws = new WebSocket(this.servAddr);

        this.ws.binaryType = 'arraybuffer';
        let self = this;
        this.ws.onopen = (evt) => {
            this.isConnecting = true;
            this.isByActive = false;
            //
            if(self.onWebSocketOpenCallBack){
                self.onWebSocketOpenCallBack(evt);
            }

        };
        this.ws.onmessage = (evt) => {
            //
            self._ondata(evt.data, evt.flags);
        };
        this.ws.onerror = (evt) => {
            //
            self.isConnecting = false;
            log('info', 'dtapiserv ' + self.servAddr + ' error.');
            log("net conneting err", " ");
        };
        this.ws.onclose = function(evt) {
            ///
            self.isConnecting = false;
            log('info', 'dtapiserv ' + self.servAddr + ' close.');
            log("net conneting fail", " ");
            self.ws = null;
            if(self.onWebSocketOnClose && self.isByActive == false){
                self.onWebSocketOnClose(evt);
            }
            self.stopReConnect();
            self.reConnect();
        };
    }
    //
    reConnect() {
        this.reconnectTimes++;
        log("this.reconnectTimes -- ",this.reconnectTimes);
        if(this.reconnectTimes >= 5){
            return;
        }
        this.connect();
    }
    //
    stopReConnect() {

    }

    //Object
    createObject(maintype,subtype){
        let obj;
        if(this.clientdata[maintype][subtype] && this.clientdata[maintype][subtype][1]){
            obj = this.clientdata[maintype][subtype][1].create();
        }
        return obj;
    }

    close() {
        log("The client initiatively closes the network connection.", " ");
        if (this.ws != null) {
            log("this.ws closeing"," ");
            this.ws.close();
            log("this.ws closed"," ");
            //this.ws = null;
        }
        if(this.heartTimer != null){
            this.stopHeartTimer();
        }
    }
    //
    closebyclient() {
        log("The client initiatively closes the network connection.", " ");
        if (this.ws != null) {
            this.isByActive = true;
            this.reconnectTimes = 5;
            log("this.ws closeing"," ");
            this.ws.close();
            log("this.ws closed"," ");
            //this.ws = null;
        }
        if(this.heartTimer != null){
            this.stopHeartTimer();
        }
    }
    //
    sendmsg(maintype, subtype, msg, cb) {
        if (this.ws != null) {
            var buf = this.clientdata[maintype][subtype][1].encode(msg).finish();

            let header = new Uint8Array(HEADERLEN);
            header[0] = maintype;
            header[1] = subtype;
            setUint16(header, 2, buf.length);
            setUint32(header, 4, 0);
            setUint16(header, 8, 0);
            this.ws.send(appendBytes(header, buf));
        }
    }

    //
    _ondata(data, flags) {
        this.parseMsg(data);
    }
    parseHeader(buf) {
        return {
            maintype: buf[0],
            subtype: buf[1],
            len: getUint16(buf, 2)

        };
    }
    parseMsg(data) {
        this.msgbuf = null;
        if (this.msgbuf == null) {
            this.msgbuf = new Uint8Array(data);
        }
        else {
            this.msgbuf = appendBytes(this.msgbuf, new Uint8Array(data));
        }

        if (this.msgbuf.length >= HEADERLEN) {
            let header = this.parseHeader(this.msgbuf);
            if (header.len + HEADERLEN == this.msgbuf.length) {

                if (header.len > 0) {
                    let buf = this.msgbuf.subarray(HEADERLEN, header.len + HEADERLEN);
                    this.onMsg(header, buf);
                }
                else {
                    this.onMsg(header, null);
                }

            }
            else if (header.len + HEADERLEN < this.msgbuf.length) {
                return ;
            }
            else {
                if (header.len > 0) {
                    let buf = this.msgbuf.subarray(HEADERLEN, header.len + HEADERLEN);
                    this.onMsg(header, buf);
                }
                else {
                    this.onMsg(header, null);
                }

                this.msgbuf = this.msgbuf.subarray(HEADERLEN + header.len, this.msgbuf.length - header.len);
            }
        }
    }
    onMsg(header, buf) {
        //  
        if (header.maintype == pb.MainType.MainBase && header.subtype == pb.SubBaseType.SubKeepAliveRsp){
            //this.getCallBack(header,buf);
            var msg = pb.KeepAliveResponse.decode(buf, header.len);
            this.onKeepAliveResponse(msg);
        }else {
            if(header.maintype == pb.MainType.MainAccount) {
                if (header.subtype == pb.SubAccountType.SubAccountLoginRsp || header.subtype == pb.SubAccountType.SubLoginRsp) {
                    this.onKeepAliveRequest();
                }
            }
            this.getCallBack(header, buf);
        }

    }

    /***************************************************************************************************************/
    onKeepAliveRequest() {
        if(this.heartTimer == null){
            let count = 0;
            let self = this;
            self.startHeartTimer();
            this.heartTimer = setInterval(function () {
                count++;
                if(count > self.heartAcceptTime && self.hasAcceptMsg == false){
                    //
                    log("10 seconds later, no heartbeat was returned.", " ");
                    count = 0;
                    self.stopHeartTimer();
                    self.isConnecting = false;
                    self.close();
                    self.clientdata[pb.MainType.MainBase][pb.SubBaseType.SubErrRsp][0]();
                }
                if(count > self.heartTime){
                    count = 0;
                    self.startHeartTimer();
                }
            },1000);
        }

    }
    // -- 
    startHeartTimer() {
        if(this.isConnecting == true){
            log("send heartbeat", " ");
            this.hasAcceptMsg = false;
            let msg = this.createObject(pb.MainType.MainBase, pb.SubBaseType.SubKeepAliveReq);
            msg.version = this.version;
            this.sendmsg(pb.MainType.MainBase, pb.SubBaseType.SubKeepAliveReq, msg);
        }
    }
    // -- 
    onKeepAliveResponse(msg) {
        log("Accept heartbeat message",timestampToTime(msg.timeNow));
        this.hasAcceptMsg = true;
    }
    stopHeartTimer() {
        if(this.heartTimer){
            log("remove heartbeat", " ");
            clearInterval(this.heartTimer);
            this.heartTimer = null;
        }
    }
    /***************************************************************************************************************/

}

function timestampToTime(timestamp) {
    var date = new Date(timestamp * 1000);//10*1000131000
    var Y = date.getFullYear() + '-';
    var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
    var D = date.getDate() + ' ';
    var h = date.getHours() + ':';
    var m = date.getMinutes() + ':';
    var s = date.getSeconds();
    return Y+M+D+h+m+s;
}