import { DTCommonClient } from './dtcommonclient';
import { dtsdkserv as pb } from './proto';
import { config, log } from './util';

var client = null;
const VER = '1.0.0';

// onNoticeMsg(msg)
function init(servaddr) {
    log('info', 'dtapi ' + VER + ' init...');

    if (servaddr == undefined || servaddr == '') {
        return ;
    }

    if (client != null) {
        client.close();
    }

    client = new DTCommonClient(servaddr);
    client.connect();
}

function onWebSocketState(onOpen,onMessage,onError,onClose) {
    client.onWebSocketOpenCallBack = onOpen;
    client.onWebSocketOnMessage = onMessage;
    client.onWebSocketOnError = onError;
    client.onWebSocketOnClose = onClose;
}

function createObject(maintype,subtype) {
    var obj = client.createObject(maintype,subtype);
    return obj;
}

function pushCallBack(maintype,subtype,callback) {
    client.pushCallBack(maintype,subtype,callback);
}

function sendMsgToDtsdk(maintype,subtype,msg,callback) {
    client.sendmsg(maintype,subtype,msg,callback);
}

//
function getConneting() {
    let isconneting = false;
    if(client){
        isconneting = client.isConnecting;
    }
    return isconneting;
}
//
function getVersion() {
    if(client){
        return client.version;
    }
    return "";
}
//
function clientType() {
    if(client){
        return client.clienttype;
    }
    return "";
}
//dtsdkdtsdk
function getDtsdkclient() {
    return client;
}

export {

    // dtsdklog
    config,

    // enum
    // 
    pb,

    // dtsdk
    init,
    onWebSocketState,
    createObject,
    pushCallBack,
    sendMsgToDtsdk,

    getConneting,
    getVersion,
    clientType,
    getDtsdkclient,
};